<template>
  <div class="order-payment">
    <div class="order-payment__tips">确定支付后不支持退款，请仔细确认订单支付方式</div>
    <order-detail-card>
      <div class="order-payment__method__title">支付方式</div>
      <van-cell title-class="order-payment__method__name">
        <template #title>
          <bitcoin-logo size="20" name="usdt" />
          <span>USDT支付</span>
        </template>
        <template #right-icon>
          <van-checkbox :modelValue="true" />
        </template>
      </van-cell>
    </order-detail-card>
    <div class="order-payment__tips order-payment__tips--center">
      <div>
        <van-checkbox :modelValue="true" icon-size="16" />
      </div>
      <span>&nbsp;&nbsp;同意&nbsp;&nbsp;</span>
      <custom-link to="/agreements/ipfs-risk">服务协议及风险</custom-link>
    </div>
    <div class="order-payment__tips order-payment__tips--center">投资有风险入市需谨慎</div>
  </div>
  <div class="order-payment__actions__placeholder"></div>
  <div class="order-payment__actions">
    <div class="order-payment__total-price">
      <div>总价</div>
      <div class="order-payment__total-price__value">{{ zhifushuliang }}</div>
      <div>USDT</div>
    </div>
    <div>
      <van-button type="primary" @click="agreementPopupShow = true" :loading="loading">
        确认支付
      </van-button>
    </div>
  </div>
  <paypassword-popup
    v-model:show="paypasswordPopupShow"
    :loading="loading"
    @confirm="onConfirm"
  ></paypassword-popup>
  <order-payment-agreement-popup
    v-model:show="agreementPopupShow"
    @confirm="paypasswordPopupShow = true"
  />
</template>

<script lang="ts">
import { defineComponent, ref } from "vue"
import OrderDetailCard from "@/views/order/OrderDetailCard.vue"
import { Button, Cell, Checkbox, Toast } from "vant"
import BitcoinLogo from "@/components/asset/BitcoinLogo.vue"
import CustomLink from "@/components/CustomLink.vue"
import PaypasswordPopup from "@/components/PaypasswordPopup.vue"
import OrderService from "@/services/order"
import { useRouter } from "vue-router"
import OrderPaymentAgreementPopup from "@/views/order/OrderPaymentAgreementPopup.vue"

export default defineComponent({
  name: "OrderPayment",
  components: {
    OrderPaymentAgreementPopup,
    PaypasswordPopup,
    CustomLink,
    BitcoinLogo,
    OrderDetailCard,
    [Button.name]: Button,
    [Cell.name]: Cell,
    [Checkbox.name]: Checkbox,
  },
  props: {
    orderNo: String,
    zhifushuliang: String,
  },
  setup(props) {
    const { go } = useRouter()
    const loading = ref(false)
    const paypasswordPopupShow = ref(false)
    const agreementPopupShow = ref(false)

    function onConfirm(paypassword: string) {
      loading.value = true
      OrderService.payOrder({
        orderNo: props.orderNo,
        paypassword,
      })
        .then(({ status, message }) => {
          Toast(message)
          if (status !== 200) {
            return
          }
          paypasswordPopupShow.value = false
          go(0)
        })
        .finally(() => {
          loading.value = false
        })
    }

    return {
      loading,
      paypasswordPopupShow,
      agreementPopupShow,
      onConfirm,
    }
  },
})
</script>
