<template>
  <!-- 订单展示 -->
  <div class="order-detail">
    <!-- 订单主页 头部 导航栏 -->
    <van-nav-bar
      title="订单详情"
      left-text=""
      left-arrow
      :right-text="order.status === '待支付' ? '取消订单' : ''"
      fixed
      placeholder
      @click-left="$router.back()"
      @click-right="onCancel()"
    />

    <div class="order-detail__header">
      <div class="order-detail__header__top">
        <div class="order-detail__header__icon">
          <van-image round width="60" height="60" :src="require('./order-status.png')" />
        </div>
        <div class="order-detail__header__top__msg">
          <div class="order-detail__header__status">{{ order.status ?? "" }}</div>
          <div class="order-detail__header__time">
            <span class="order-detail__header__time__title">创建时间：</span>
            <span class="order-detail__header__time__value">{{ order.chuangjiantime ?? "" }}</span>
          </div>
        </div>
      </div>

      <div class="order-detail__header__bottom">
        <div class="order-detail__header__no">
          <span class="order-detail__header__no__title">订单号&nbsp;&nbsp;</span>
          <div class="order-detail__header__no__value van-ellipsis">{{ order.orderNo ?? "" }}</div>
        </div>
        <div class="order-detail__header__copy" @click="onCopyToClipboard">复制</div>
      </div>
    </div>

    <!-- machine info one -->
    <order-detail-card>
      <order-detail-cell :title="order.productname" bold />
      <order-detail-cell title="购买台数">
        <template #default> x{{ order.number }}</template>
      </order-detail-cell>
      <order-detail-cell title="矿机总价">
        <template #default> {{ order.zhifushuliang }}USDT</template>
      </order-detail-cell>
      <order-detail-cell title="服务周期">
        <template #default> {{ order?.fuwuzhouqi }}天</template>
      </order-detail-cell>
      <div class="order-detail__total-price">
        <div class="order-detail__total-price__title">总价</div>
        <div class="order-detail__total-price__value">{{ order.zhifushuliang }}</div>
        <div class="order-detail__total-price__unit">USDT</div>
      </div>
    </order-detail-card>

    <!-- machine info Two -->
    <order-detail-card>
      <!--      <order-detail-cell title="最新产出" value="0" />
            <order-detail-cell title="累计产出" value="0" />
            <order-detail-cell title="" value="产出记录>>" />-->
      <order-detail-cell title="预计交付" value="最晚下单后15天" />
      <order-detail-cell class="order-detail__contract" title="合同">
        <template #default>
          <div @click="$router.push('/agreements/ipfs-service')">《IPFS算力管理云服务协议》</div>
          <div @click="$router.push('/agreements/ipfs-sales')">《IPFS独立满存算力销售协议》</div>
        </template>
      </order-detail-cell>
    </order-detail-card>
    <order-payment
      v-if="order.status !== '生效中'"
      :order-no="orderNo"
      :zhifushuliang="order.zhifushuliang"
    />
  </div>
</template>

<script lang="ts">
import copyToClipboard from "copy-to-clipboard"
import { defineComponent, onMounted, ref } from "vue"
import { Button, Cell, CellGroup, Dialog, Image as VanImage, NavBar, Toast } from "vant"
import OrderDetailCard from "./OrderDetailCard.vue"
import OrderDetailCell from "./OrderDetailCell.vue"
import OrderService, { Order } from "@/services/order"
import OrderPayment from "@/views/order/OrderPayment.vue"
import { useRouter } from "vue-router"

export default defineComponent({
  name: "OrderDetail",
  components: {
    OrderPayment,
    OrderDetailCard,
    OrderDetailCell,
    [NavBar.name]: NavBar,
    [Toast.name]: Toast,
    [Cell.name]: Cell,
    [CellGroup.name]: CellGroup,
    [Button.name]: Button,
    [VanImage.name]: VanImage,
  },
  props: {
    orderNo: String,
  },
  setup(props) {
    const { go } = useRouter()
    const order = ref<Order>({})

    function refreshOrder() {
      const loadingToast = Toast({
        type: "loading",
        forbidClick: true,
        loadingType: "spinner",
      })
      OrderService.getOrder(props.orderNo)
        .then(({ status, message, data }) => {
          if (status === 200) {
            order.value = data
          } else {
            Toast(message)
          }
        })
        .finally(() => {
          loadingToast.clear()
        })
    }

    function onCopyToClipboard() {
      if (props.orderNo) {
        copyToClipboard(props.orderNo)
      }
      Toast("复制成功")
    }

    function onCancel() {
      Dialog.confirm({
        title: "是否取消订单",
        message: "是否确认取消订单，订单取消后将无法付款，如对订单有疑问请及时咨询客服。",
        cancelButtonText: "我再看看",
        confirmButtonText: "确认取消",
      }).then(() => {
        OrderService.cancelOrder(props.orderNo).then(({ status, message }) => {
          Toast(message)
          if (status === 200) {
            go(0)
          }
        })
      })
    }

    onMounted(refreshOrder)
    return {
      order,
      onCancel,
      onCopyToClipboard,
    }
  },
})
</script>
