<template>
  <div class="order-list">
    <van-nav-bar title="订单" left-text="" left-arrow @click-left="$router.back()" />
    <div class="order-list__title">
      <div class="order-list__title__count">{{ statusCount }}</div>
      <div class="order-list__title__hint">持有矿机数（台）</div>
    </div>
    <van-tabs
      class="order-list__nav"
      :active="status"
      color="#1989fa"
      title-active-color="#1989fa"
      animated
      @change="(name) => $router.replace(`/orders?status=${name}`)"
    >
      <van-tab name="待支付" title="待支付">
        <order-list-panel v-if="status === '待支付'" :status="status" />
      </van-tab>
      <van-tab name="生效中" title="生效中">
        <order-list-panel v-if="status === '生效中'" :status="status" />
      </van-tab>
      <van-tab name="已终止" title="已终止">
        <order-list-panel v-if="status === '已终止'" :status="status" />
      </van-tab>
    </van-tabs>
  </div>
</template>

<script lang="ts">
import { computed, defineComponent, onMounted, ref } from "vue"
import { Divider, NavBar, Tab, Tabs, Toast } from "vant"
import OrderListPanel from "@/views/order/OrderListPanel.vue"
import OrderService, { OrderCount } from "@/services/order"

export default defineComponent({
  name: "OrderList",
  components: {
    OrderListPanel,
    [NavBar.name]: NavBar,
    [Tab.name]: Tab,
    [Tabs.name]: Tabs,
    [Divider.name]: Divider,
  },
  props: {
    status: {
      type: String,
      default: "生效中",
    },
  },
  setup(props) {
    const orderCount = ref<OrderCount>()

    const statusCount = computed(() => {
      if (props.status === "待支付") {
        return orderCount.value?.arrearage
      } else if (props.status === "生效中") {
        return orderCount.value?.paid
      } else if (props.status === "已终止") {
        return orderCount.value?.end
      }
      return 0
    })

    onMounted(() => {
      OrderService.countOrders().then(({ status, message, data }) => {
        if (status !== 200) {
          Toast(message)
        }
        orderCount.value = data
      })
    })
    return {
      statusCount,
    }
  },
})
</script>
