
import { defineComponent } from "vue"
import ServiceAgreementContent from "@/views/agreement/ServiceAgreementContent.vue"
import { Popup } from "vant"

export default defineComponent({
  name: "OrderPaymentAgreementPopup",
  components: {
    ServiceAgreementContent,
    [Popup.name]: Popup,
  },
  props: {
    show: {
      type: Boolean,
      default: false,
    },
  },
  setup(props, { emit }) {
    function onConfirm() {
      emit("update:show", false)
      emit("confirm")
    }

    return {
      onConfirm,
    }
  },
})
