<template>
  <div class="order-list-card" @click="$router.push(`/orders/${orderNo}`)">
    <div class="order-list-card__header">
      <div class="order-list-card__no van-ellipsis">订单号: {{ orderNo }}</div>
      <div class="order-list-card__status">{{ status }}</div>
    </div>
    <van-divider />
    <div class="order-list-card__content">
      <div class="order-list-card__cell">
        <div class="order-list-card__title">{{ productname }}</div>
        <div class="order-list-card__value">x{{ number }}</div>
      </div>
      <div class="order-list-card__cell" v-if="paid">
        <div class="order-list-card__cell__title">总金额</div>
        <div class="order-list-card__cell__value">{{ zhifushuliang }}USDT</div>
      </div>
      <div class="order-list-card__cell" v-if="paid">
        <div class="order-list-card__cell__title">类型</div>
        <div class="order-list-card__cell__value">{{ producttype }}</div>
      </div>
      <!--      <div class="order-list-card__cell" v-if="paid">
        <div class="order-list-card__cell__title">最新产出</div>
        <div class="order-list-card__cell__value">0</div>
      </div>
      <div class="order-list-card__cell" v-if="paid">
        <div class="order-list-card__cell__title">累计产出</div>
        <div class="order-list-card__cell__value">0</div>
      </div>-->
      <div class="order-list-card__cell" v-if="paid">
        <div class="order-list-card__cell__title">预计交付</div>
        <div class="order-list-card__cell__value">最晚下单后15天</div>
      </div>
      <div class="order-list-card__cell" v-if="paid">
        <div class="order-list-card__cell__title">结束日</div>
        <div class="order-list-card__cell__value">开挖日+540天+540天</div>
      </div>
      <div class="order-list-card__cell" v-if="paid" @click.stop="$event">
        <div class="order-list-card__cell__title">合同</div>
        <div class="order-list-card__cell__value">
          <div @click.stop="$router.push('/agreements/ipfs-service')">
            《IPFS算力管理云服务协议》
          </div>
          <div @click.stop="$router.push('/agreements/ipfs-sales')">
            《IPFS独立满存算力销售协议》
          </div>
        </div>
      </div>
      <div class="order-list-card__cell">
        <div class="order-list-card__cell__value">下单日:{{ chuangjiantime }}</div>
      </div>
    </div>
    <van-divider v-if="status === '待支付'" />
    <div v-if="status === '待支付'" class="order-list-card__actions">
      <van-button size="small" round @click.stop="onCancel()">取消订单</van-button>
      <van-button size="small" round plain type="primary">确认支付</van-button>
    </div>
  </div>
</template>

<script lang="ts">
import { computed, defineComponent } from "vue"
import { Button, Dialog, Divider, Toast } from "vant"
import OrderService from "@/services/order"
import { useRouter } from "vue-router"

export default defineComponent({
  name: "OrderListCard",
  components: {
    [Divider.name]: Divider,
    [Button.name]: Button,
  },
  props: {
    orderNo: String,
    number: [String, Number],
    status: String,
    productname: String,
    producttype: String,
    chuangjiantime: String,
    zhifushuliang: String,
  },
  setup(props) {
    const { go } = useRouter()
    const paid = computed(() => props.status !== "待支付")

    function onCancel() {
      Dialog.confirm({
        title: "是否取消订单",
        message: "是否确认取消订单，订单取消后将无法付款，如对订单有疑问请及时咨询客服。",
        cancelButtonText: "我再看看",
        confirmButtonText: "确认取消",
      }).then(() => {
        OrderService.cancelOrder(props.orderNo).then(({ status, message }) => {
          Toast(message)
          if (status === 200) {
            go(0)
          }
        })
      })
    }

    return {
      paid,
      onCancel,
    }
  },
})
</script>
