
import copyToClipboard from "copy-to-clipboard"
import { defineComponent, onMounted, ref } from "vue"
import { Button, Cell, CellGroup, Dialog, Image as VanImage, NavBar, Toast } from "vant"
import OrderDetailCard from "./OrderDetailCard.vue"
import OrderDetailCell from "./OrderDetailCell.vue"
import OrderService, { Order } from "@/services/order"
import OrderPayment from "@/views/order/OrderPayment.vue"
import { useRouter } from "vue-router"

export default defineComponent({
  name: "OrderDetail",
  components: {
    OrderPayment,
    OrderDetailCard,
    OrderDetailCell,
    [NavBar.name]: NavBar,
    [Toast.name]: Toast,
    [Cell.name]: Cell,
    [CellGroup.name]: CellGroup,
    [Button.name]: Button,
    [VanImage.name]: VanImage,
  },
  props: {
    orderNo: String,
  },
  setup(props) {
    const { go } = useRouter()
    const order = ref<Order>({})

    function refreshOrder() {
      const loadingToast = Toast({
        type: "loading",
        forbidClick: true,
        loadingType: "spinner",
      })
      OrderService.getOrder(props.orderNo)
        .then(({ status, message, data }) => {
          if (status === 200) {
            order.value = data
          } else {
            Toast(message)
          }
        })
        .finally(() => {
          loadingToast.clear()
        })
    }

    function onCopyToClipboard() {
      if (props.orderNo) {
        copyToClipboard(props.orderNo)
      }
      Toast("复制成功")
    }

    function onCancel() {
      Dialog.confirm({
        title: "是否取消订单",
        message: "是否确认取消订单，订单取消后将无法付款，如对订单有疑问请及时咨询客服。",
        cancelButtonText: "我再看看",
        confirmButtonText: "确认取消",
      }).then(() => {
        OrderService.cancelOrder(props.orderNo).then(({ status, message }) => {
          Toast(message)
          if (status === 200) {
            go(0)
          }
        })
      })
    }

    onMounted(refreshOrder)
    return {
      order,
      onCancel,
      onCopyToClipboard,
    }
  },
})
