
import { defineComponent, ref } from "vue"
import OrderDetailCard from "@/views/order/OrderDetailCard.vue"
import { Button, Cell, Checkbox, Toast } from "vant"
import BitcoinLogo from "@/components/asset/BitcoinLogo.vue"
import CustomLink from "@/components/CustomLink.vue"
import PaypasswordPopup from "@/components/PaypasswordPopup.vue"
import OrderService from "@/services/order"
import { useRouter } from "vue-router"
import OrderPaymentAgreementPopup from "@/views/order/OrderPaymentAgreementPopup.vue"

export default defineComponent({
  name: "OrderPayment",
  components: {
    OrderPaymentAgreementPopup,
    PaypasswordPopup,
    CustomLink,
    BitcoinLogo,
    OrderDetailCard,
    [Button.name]: Button,
    [Cell.name]: Cell,
    [Checkbox.name]: Checkbox,
  },
  props: {
    orderNo: String,
    zhifushuliang: String,
  },
  setup(props) {
    const { go } = useRouter()
    const loading = ref(false)
    const paypasswordPopupShow = ref(false)
    const agreementPopupShow = ref(false)

    function onConfirm(paypassword: string) {
      loading.value = true
      OrderService.payOrder({
        orderNo: props.orderNo,
        paypassword,
      })
        .then(({ status, message }) => {
          Toast(message)
          if (status !== 200) {
            return
          }
          paypasswordPopupShow.value = false
          go(0)
        })
        .finally(() => {
          loading.value = false
        })
    }

    return {
      loading,
      paypasswordPopupShow,
      agreementPopupShow,
      onConfirm,
    }
  },
})
