<template>
  <div class="order-list-panel__content">
    <van-pull-refresh v-model="loading" @refresh="refreshOrders">
      <!--      <van-list v-model:loading="loading">
              <order-list-card
                v-for="order in orders"
                :key="order.orderNo"
                :orderNo="order.orderNo"
                :status="order.status"
                :productname="order.productname"
              />
            </van-list>-->
      <van-empty v-if="!loading && ordersEmpty" description="暂无记录" />
      <template v-if="!ordersEmpty">
        <order-list-card
          v-for="order in orders"
          :key="order.orderNo"
          :orderNo="order.orderNo"
          :number="order.number"
          :status="order.status"
          :productname="order.productname"
          :producttype="order.producttype"
          :chuangjiantime="order.chuangjiantime"
          :zhifushuliang="order.zhifushuliang"
        />
      </template>
    </van-pull-refresh>
    <div v-if="!ordersEmpty" class="order-list-panel__no-more">没有更多了</div>
  </div>
</template>

<script lang="ts">
import { computed, defineComponent, ref, watchEffect } from "vue"
import { Divider, Empty, List, NavBar, PullRefresh, Tab, Tabs } from "vant"
import OrderListCard from "./OrderListCard.vue"
import OrderService, { Order } from "@/services/order"
import _ from "lodash"

export default defineComponent({
  name: "OrderListPanel",
  components: {
    OrderListCard,
    [NavBar.name]: NavBar,
    [Tab.name]: Tab,
    [Tabs.name]: Tabs,
    [Divider.name]: Divider,
    [PullRefresh.name]: PullRefresh,
    [List.name]: List,
    [Empty.name]: Empty,
  },
  props: {
    status: String,
  },
  setup(props) {
    const orders = ref<Order[]>([])
    const loading = ref(false)

    function refreshOrders() {
      loading.value = true
      OrderService.getOrders({ status: props.status })
        .then(({ data }) => {
          orders.value = data
        })
        .finally(() => {
          loading.value = false
        })
    }

    watchEffect(refreshOrders)

    const ordersEmpty = computed(() => {
      return _.isEmpty(orders.value)
    })

    return { loading, ordersEmpty, orders, refreshOrders }
  },
})
</script>
