<template>
  <van-popup
    class="paypassword-popup"
    :show="show"
    closeable
    close-icon-position="top-left"
    @close="onClose()"
    @click-close-icon="onClose()"
  >
    <van-field v-model="paypassword" type="password" center placeholder="请输入资金密码">
      <template #button>
        <van-button round size="small" type="primary" @click="onConfirm" :loading="loading">
          确定
        </van-button>
      </template>
    </van-field>
    <div class="paypassword-popup__forget">
      <custom-link to="/resets/paypassword">忘记交易密码？</custom-link>
    </div>
  </van-popup>
</template>

<script>
import { defineComponent, ref } from "vue"
import { Button, Field, Popup, Toast } from "vant"
import _ from "lodash"
import CustomLink from "@/components/CustomLink"

export default defineComponent({
  name: "PaypasswordPopup",
  components: {
    CustomLink,
    [Button.name]: Button,
    [Field.name]: Field,
    [Popup.name]: Popup,
  },
  props: {
    show: {
      type: Boolean,
      default: false,
    },
    loading: {
      type: Boolean,
      default: false,
    },
  },
  setup(props, { emit }) {
    const paypassword = ref("")

    function onConfirm() {
      if (_.isEmpty(paypassword.value)) {
        Toast("交易密码不能为空")
        return
      }
      emit("confirm", paypassword.value)
    }

    function onClose() {
      emit("update:show", false)
    }

    return {
      paypassword,
      onConfirm,
      onClose,
    }
  },
})
</script>

