<template>
  <van-popup
    class="order-payment-agreement-modal"
    closeable
    close-on-click-overlay
    :show="show"
    @click-close-icon="$emit('update:show', false)"
    @click-overlay="$emit('update:show', false)"
    @close="$emit('update:show', false)"
  >
    <div class="order-payment-agreement-modal__header">服务协议及风险</div>
    <service-agreement-content />
    <div class="order-payment-agreement-modal__action" @click.stop="onConfirm()">
      我已阅读并理解内容，确认提交
    </div>
  </van-popup>
</template>

<script lang="ts">
import { defineComponent } from "vue"
import ServiceAgreementContent from "@/views/agreement/ServiceAgreementContent.vue"
import { Popup } from "vant"

export default defineComponent({
  name: "OrderPaymentAgreementPopup",
  components: {
    ServiceAgreementContent,
    [Popup.name]: Popup,
  },
  props: {
    show: {
      type: Boolean,
      default: false,
    },
  },
  setup(props, { emit }) {
    function onConfirm() {
      emit("update:show", false)
      emit("confirm")
    }

    return {
      onConfirm,
    }
  },
})
</script>

